body{
    background-color: #f4f4f4;
}
#profilecontent{
    padding-top: 100px;
    background-color: #f4f4f4;
}
.profile_row {
    padding-top: 35px;
    padding-bottom: 35px;
    border-bottom: 0.5px solid #9c9c9c;
}
.sologn_desc {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 30px;
    letter-spacing: 0.1em;
    color: #494848;
}
.pr_pic {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
}
.pr_pic2 img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.pr_pic2 {
    width: 160px;
    height: 160px;
    background: #ffffff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    background-color: red;
}
.prof_stats {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 0.5px solid #494848;
}
.prof_stats span {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 30px;
    letter-spacing: 0.1em;
    color: #494848;
}
.picdiv_para {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    padding: 28px 20px 70px 40px;
    flex-wrap: wrap;
    gap: 1rem;
}
.pic_div {
    width: 400px;
    height: 400px;
    background: #c4c4c4;
}
.pic_div img{
    width: 100%;
    height: 100%;
}